@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Manrope&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body {
  margin: 0;
  font-family: "Manrope";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(253, 247, 234);
  font-size: 0.9rem;
}

input {
  all: unset;
  border: 1px solid black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer {
  background-color: black;
  color: white;
  padding: 5px 0;
  text-align: center;
  margin-top: 15px;
}

.footer a {
  color: unset;
  text-decoration: none;
}

button {
  border: 1px solid transparent;
  box-shadow: 0px 1px 2px 1px #b0a1a1;
}

button:disabled:hover {
  background-color: white;
  cursor: auto;
  cursor: initial;
}
.app-section {
  display: grid;
  grid-template-areas:
  "progress progress"
  "form preview"
  "navigation preview";
}

@media (max-width: 1148px) {
  .app-section {
    grid-template-areas:
    "progress"
    "form "
    "navigation"
    "preview";
  }
}


.progress-bar {
  grid-area: progress;
}

.form {
  grid-area: form;
  justify-self: center;
}

.navigation {
  grid-area: navigation;
}

.preview {
  grid-area: preview;
  justify-self: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
header.header {
  background-color: rgb(60,60,60);
  color: white;
}

.logo {
  padding: 10px;
  margin-bottom: 5px;
  margin-left: 25px;
  font-size: 200%;
}

.author {
  margin-left: 15px;
  font-size: 1rem;
}
.start-screen {
  align-items: center;
  width: 100vw;
  height: 80vh;
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.start-screen .menu button {
  display: block;
  width: 256px;
  margin: 15px 0;
}

.value-prop {
  font-size: 280%;
}

.sample-image {
  margin: 15px;
  width: 300px;
  box-shadow: 0px 4px 9px 3px #dfcdaa;
}

@media (max-width: 600px) {
  .sample-image {
    display: none;
  }
}
.progress-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 80vw;
  max-width: 1200px;
  margin: 0 auto;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.progress-bar .section.active {
  opacity: 1.0;
  transition: border 300ms;
}

.progress-bar .section.active::after {
  content: "";
  border-bottom: 1px solid black;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  -webkit-animation: deploy 300ms ease-out;
          animation: deploy 300ms ease-out;
}

@-webkit-keyframes deploy {
  from {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  to {
    -webkit-transform: scaleX(1.0);
            transform: scaleX(1.0);
  }
}

@keyframes deploy {
  from {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  to {
    -webkit-transform: scaleX(1.0);
            transform: scaleX(1.0);
  }
}

.progress-bar .section {
  padding: 5px;
  font-size: 0.95em;
  width: 100px;
  opacity: 0.5;
  position: relative;
  cursor: pointer;
  transition: text-shadow 0.3s;
}

.progress-bar .section:hover {
  text-shadow: 0px 2px 9px #292703;
}

.index {
  display: block;
  text-align: center;
  border: 1px dotted rgb(128, 128, 128);
  width: 16px;
  height: 16px;
  padding: 2px;
  border-radius: 50%;
  margin: 0 auto;
}

.name {
  text-align: center;
}
@media print {
  body * {
    visibility: hidden;
  }
  #section-to-print, #section-to-print * {
    visibility: visible;
  }
  #section-to-print {
    position: absolute;
    left: 0;
    top: 0;
    border: none;
    -webkit-print-color-adjust: exact;
    box-shadow: none;
    overflow-y: hidden;
    height: -webkit-fit-content;
    height: fit-content;
    height: -moz-fit-content;
    max-height: none;
    max-height: initial;
    width: 100vw;
  }
}
.section-title {
  font-size: 175%;
  -webkit-font-feature-settings: "smcp", "c2sc";
          font-feature-settings: "smcp", "c2sc";
  font-variant-caps: all-small-caps;
}

.form {
  min-width: 128px;
  width: 30vw;
  max-width: 650px;
  max-height: 80vh;
  overflow-y: auto;
}

@media (max-width: 1148px) {
  .form {
    width: 90vw;
    max-height: 50vh;
  }
}

.form-section {
  padding: 15px;
}

.subsections {
  overflow-y: auto;
  overflow-x: hidden;
}

.section-title {
  text-align: center;
}

.form .label, .form .input, .input-list {
  display: block;
  width: 100%;
}

.input, .input-list {
  border: none;
  border-bottom: 1px solid #b4b4b3ab;
  -webkit-font-feature-settings: "kern" off;
          font-feature-settings: "kern" off;
  font-variant: none;
  transition: border 300ms;
}

.input-list {
  margin: 10px 0px;
  width: 100%;
}

.input-list-container button {
  padding: 2px 10px;
  width: 100px;
  margin-right: 15px;
  margin-bottom: 5px;
  font-size: 90%;
}

.form .label, .input-list-container {
  margin: 15px;
  width: 95%;
}

.input:focus, .input-list:focus {
  border-bottom: 1px solid orange;
}

.form .label, .form .label-display{
  -webkit-font-feature-settings: "pcap", "c2pc";
          font-feature-settings: "pcap", "c2pc";
  font-variant: all-petite-caps;
  font-size: 110%;
}

.subsections > div {
  margin: 0 5px;
  padding-top: 15px;
  padding-bottom: 15px;
  border-bottom: 3px dotted #cb8c56;
}

.remove-subsection, .add-subsection {
  display: block;
  width: 60%;
  max-width: 300px;
  margin: 0 auto;
}

.add-subsection {
  margin-top: 15px;
}

.review-section {
  position: relative;
  top: 50%;
  text-align: center;
}

@media (max-width: 768px) {
  .review-section {
    top: 0;
    margin-top: 15px;
  }
}

.review-section .caption {
  font-size: 200%;
}

.review-section .controls > * {
  margin: 25px;
  font-size: 125%;
}

.controls .print {
  background-color: rgb(255, 202, 103);
}

.controls .print:hover {
  background-color: rgb(251, 178, 41);
}
.preview-title, .preview-note {
  text-align: center;
}

.preview-title {
  font-weight: bold;
  margin: 4px 25%;
  padding-bottom: 5px;
}

.resume {
  font-family:'IBM Plex Sans';
  width: 90vw;
  max-width: 800px;
  --style-color: rgb(231, 107, 36);
  font-size: 18px;
  box-shadow: 0px 3px 8px 4px #c7c3c3;
  height: 80vh;
  margin: 5px;
  overflow-y: auto;
  max-height: 1150px;
  background-color: white;
}

/* General begin */
.resume-section {
  margin: 0.75em;
  padding: 0.2em 0 1.25em 0;
  display: flex;
  flex-direction: row;
}

.resume-section:not(:nth-last-child(1)) {
  border-bottom: 1px solid var(--style-color);
}

.resume-section .title {
  padding: 0 0.25em;
  min-width: 128px;
  font-size: 90%;
  font-weight: 600;
}

.resume-section .contents {
  width: 100%;
}

.resume-section .contents .item {
  margin-bottom: 0.75em;
}

.title-icon {
  color: var(--style-color);
  min-width: 1.4em;
}

.header-title {
  font-weight: bold;
}

.contents .item > *:not(.header) {
  font-size: 90%;
  margin: .5em 0;
}

.resume .list .list-item {
  list-style-type: square;
  position: relative;
  left: 0.8em;
  margin: 0.8em;
  font-size: 95%;
}

/* General end */

/* Personal Section Begin */
.personal {
  padding: 0.3em;
  text-align: center;
  background-color: var(--style-color);
  color: white;
}

.fullname {
  font-size: 300%;
  -webkit-font-feature-settings: "pcap", "c2pc";
          font-feature-settings: "pcap", "c2pc";
  font-variant-caps: all-petite-caps;
  font-weight: 600;
  letter-spacing: 0.5mm;
}

.contact-address > * {
  display: inline-block;
  margin: 0 8px;
  font-size: 90%;
}

.icon {
  font-size: 80%;
  margin: 0 5px;
  display: inline-block;
}

.address .icon {
  vertical-align: top;
  position: relative;
  top: 2px;
  left: 1px;
}

.contact-info > p, .address {
  margin: 8px 0;
}

.address-text {
  display: inline-block;
}

/* Personal Section End */

/* Education Resume Begin */


.contents .item .header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.school-name {
  font-weight: 500;
}

.degree-gpa {
  font-weight: 600;
}

/* Education Resume End */

/* Skills Section Begin */
.review-section.section {

}

.skill-category {
  font-weight: bold;
}

.skill {
  margin-right: 10px;
}

.skills {
  position: relative;
  left: 1px;
}

.skill-item {
  list-style-type: square;
  font-size: 85%;
  margin: 10px 25px;
}

.preview .skills-section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

/* Skills Section End */

.navigation {
  align-self:end;
}

.buttons {
  text-align: center;
}

.navigation button {
  width: 35%;
  max-width: 250px;
  margin: 0 10px;
  border: 1px solid transparent;
  box-shadow: 0px 1px 2px 1px #b0a1a1;
}



button {
  font-family: inherit;
  background-color: white;
  padding: 2px 15px;
  border-radius: 4px;
  font-size: 105%;
  transition: background-color 300ms;
  cursor: pointer;
  
}

button:hover {
  background-color: rgb(208, 188, 145);
}
